<template>
  <div class="hello">
    <div style="width: 80%">
      <ve-histogram :data="populationData"></ve-histogram>
      <ve-line :data="populationData"></ve-line>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
  export default {
    data () {
      return {
        populationData: {
          columns: ['年度', '總人口數','男性人數','女性人數'],
          rows: []
        },
        chartData: {
          columns: ['日期', '访问用户', '下单用户', '下单率'],
          rows: [
            { '日期': '1/1', '访问用户': 1393, '下单用户': 1093, '下单率': 0.32 },
            { '日期': '1/2', '访问用户': 3530, '下单用户': 3230, '下单率': 0.26 },
            { '日期': '1/3', '访问用户': 2923, '下单用户': 2623, '下单率': 0.76 },
            { '日期': '1/4', '访问用户': 1723, '下单用户': 1423, '下单率': 0.49 },
            { '日期': '1/5', '访问用户': 3792, '下单用户': 3492, '下单率': 0.323 },
            { '日期': '1/6', '访问用户': 4593, '下单用户': 4293, '下单率': 0.78 }
          ]
        }
      }
    },
    methods: {
      callUvData: function() {
        // let url = './api'
        const cors = 'https://cors-anywhere.herokuapp.com/';
        let url = 'http://opendataap2.hl.gov.tw/resource/files/2019-03-15/b926a88f68cf8f8f478b0d2f5ac98ac4.json'
        console.log('test')
        axios.get(cors+url)
        .then(response => {
          console.log(response)
          response.data.forEach(item => {
            let obj = {
              '年度': item['年度'],
              '總人口數': item['總人口數'],
              '男性人數': item['男性人數'],
              '女性人數': item['女性人數']
            }
            this.populationData.rows.push(obj)
            // console.log(item['總人口數'])
          })
        })
        .catch()
      }
    },
    mounted() {
      this.callUvData()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



